'use strict';

import angular from 'angular';
import 'angular-material';
import mainwrap from 'common/directives/mainwrap/mainwrap';
import boonsCtrl from './boons.controller';
import './boons.tpl';
import './boons.header.tpl';
import './boons.css!';

const boonsModule = angular.module('boons', [
  mainwrap.name,
  'app/boons/boons.tpl.html',
  'app/boons/boons.header.tpl.html',
  boonsCtrl.name
]);

boonsModule.config(function($stateProvider){
  $stateProvider.state('boons', {
    url: '/boons',
    templateUrl: 'app/boons/boons.tpl.html',
    controller: 'BoonsCtrl',
    controllerAs: 'boonsCtrl',
    authenticate: false
  });
});

export default boonsModule;
